import { createWebHistory, createRouter } from "vue-router";
import List from './components/List.vue'
import Home from './components/Home.vue'
import Detail from './components/Detail.vue'
import Author from './components/Author.vue'
import Comment from './components/Comment.vue'
const routes = [
  {
    //경로로 들어오면 보여줄 컴포넌트 연결하기
    //초기 세팅을 하고나면 이제 페이지를 나눌 땐 아래 두 개만 건들면 됨
    //라우터를 추가할 때도 아래에 추가 경로를 설정해주면 됨
    path: "/list",
    component: List,
  },
  {
    path: "/",
    component: Home,
  },
  {
    // /url/:x -> x번 게시물 보여주기, 여러개 등록 가능
    //{{$route.params.id}}로 가져와서 사용할 수 있음
    //파라미터 업그레이드 가능 id() -> 괄호안에 정규식 입력가능(파라미터 값 제한 가능), 필묘하면 vue-router4 문서 찾아보셈
    path: "/detail/:id", 
    component: Detail,
    //route안에 route를 나눌 수 있음(경로 이어서 작성)
    //특정 페이지 안에서 route를 나누고 싶을 때 nested routes를 쓰셈 => 아래 children으로 나누는거
    children:[
        { // /로 시작하는 문구는 홈페이지란 뜻이므로 상대경로로 적어서 이동한다.
            path:"author",
            component:Author.vue,

        },
        {
            path:"comment",
            component:Comment.vue,
        }
    ]
  }
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router; 

/*
라이터 첫 셋팅은
1. 설치하고 router.js 복붙하고
2. <router-view> 이것도 넣고 
*/